.bg {
  background: black;
  background-image: -webkit-gradient(linear, left top, left bottom, color-stop(.1em, #2d9bec), color-stop(.1em, transparent)), -webkit-gradient(linear,left top, right top,color-stop(.1em, #2d9bec), color-stop(.1em, transparent));
  background-image: -webkit-linear-gradient(#2d9bec .1em, transparent .1em), -webkit-linear-gradient(left,#2d9bec .1em, transparent .1em);
  background-image: -o-linear-gradient(#2d9bec .1em, transparent .1em), -o-linear-gradient(left,#2d9bec .1em, transparent .1em);
  background-image: linear-gradient(#2d9bec .1em, transparent .1em), linear-gradient(90deg,#2d9bec .1em, transparent .1em);
  background-size: .618rem .618rem;
}

.logo {
  background: -webkit-linear-gradient(#fcb559, #fc5300);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 82px;
}

.desc {
  color: white;
}


